import clone from '@/lib/clone';
import Vue from 'vue'
import Vuex from 'vuex'
import recordStore from './recordStore'

Vue.use(Vuex)   // vue.prototype.$storestore

const store = new Vuex.Store({
  state: {  // data
    recordList:[] as RecordItem[],  // 
    tagList:[] as Tag[]
  },
  mutations:{  // methods
    fetchRecords(state){   // 
      const recordList=JSON.parse(window.localStorage.getItem('recordList') || '[]')
      state.recordList=recordList
    },
    createRecord(state,record){  // 
      const record2:RecordItem=clone(record)  // record,record
      record2.createdAt=new Date().toISOString()  // 
      state.recordList.push(record2)  // push
      store.commit('saveRecords')  
    },
    saveRecords(state){  // 
      window.localStorage.setItem('recordList',JSON.stringify(state.recordList))
    },
    fetchTags(state){
      const tagList=JSON.parse(window.localStorage.getItem('localStorageKeyName' || '[]'))
      if(!state.tagList || state.tagList.length===0){   // ,
        store.commit('createTag','')
        store.commit('createTag','')
        store.commit('createTag','')
        store.commit('createTag','')
      }
      return state.tagList=tagList
    },
    // createTag(state,name:string){
    //   const names=state.tagList.map(item=>item.name)

    //   // tagListModelname(name successduplicated)
    //   const message=tagListModel.create(name)
    //   if(message==='duplicated'){
    //     window.alert('')
    //   }else if(message==='success'){
    //     window.alert('')
    //   }
    //   location.reload()
    // },
  }
})

export default store