import createId from "@/lib/idCreateId"

const localStorageKeyName='tagList'
const tagListModel:TagListModel={  // type
    data:[],
    create(name){  // names:  name: 
        // this.data=[{id:'1',name:'1'},{id:'2',name:'2'}]
        const names=this.data.map(item=>item.name)  // names=['1','2']
        if(names.indexOf(name)<0){
            const id=createId().toString()  // idid(lib/idCreateId.ts)
            this.data.push({id,name:name})   // namename push
            this.save()
            return 'success'
        }else{
            return 'duplicated'
        }
    },
    fetch(){  // 
        this.data=JSON.parse(window.localStorage.getItem(localStorageKeyName) || '[]')
        return this.data    // return
    },
    update(id,name){
        const idList=this.data.map(item=>item.id)  // dataid
        if(idList.indexOf(id)>=0){   // iddata,
            const names=this.data.map(item=>item.name) // dataname
            if(names.indexOf(name)>=0){
                return 'duplicated'  // namedata,
            }else{  // idname,id,namename
                const tag=this.data.filter(item=>item.id===id)[0]  // ,idtag
                tag.name=name   // tagnameupdatename
                tag.id=name 
                this.save()
                return 'success'
            }
        }else{  // nameid
            return 'not found'  // ,id
        }
    },
    remove(id:string){
        let index=-1
        for(let i=0;i<this.data.length;i++){
            if(this.data[i].id===id){
                index=i  // ,data,
                break
            }
        }
        this.data.splice(index,1)
        this.save()
        return true
    },
    save(){    // 
        window.localStorage.setItem(localStorageKeyName,JSON.stringify(this.data))
    },
}

export default tagListModel