import tagListModel from "@/models/tagListModel"

export default{
        // tag store:
    // ,money.vuelabels.vue (window.tagList)
    // custom.d.ts,window.tagList
    tagList:tagListModel.fetch(),
    refreshTags() {  // 
      this.tagList = tagListModel.fetch()
    },
    findTag(id:string){  // this
      return this.tagList.filter(t=>t.id===id)[0]  // tagsididtag
    },
    createTag:(name:string)=>{
      // tagListModelname(name successduplicated)
      const message=tagListModel.create(name)
      if(message==='duplicated'){
        window.alert('')
      }else if(message==='success'){
        window.alert('')
      }
      location.reload()
    },
    removeTag:(id:string)=>{
      return tagListModel.remove(id)
      // this.$router.back()  // 
    },
    updateTag:(id:string,name:string)=>{
      return tagListModel.update(id,name)
    }
}